<template>
  <div>
    <van-row>
      <van-col span="10">
        <van-field label="" v-model="selectURL" />
        <van-field label="id" v-model="inputStageId" />
        <van-popup v-model="showLog">{{logMsg}}</van-popup>
        <iframe id="iframe" src="game.html" style="width: 100%;height: -webkit-fill-available;">
          
        </iframe>
      </van-col>

      <van-col span="14">
        
        <van-collapse v-model="activeKeys">
          <van-collapse-item
            v-for="lockandkey in keysList"
            :key="lockandkey.id"
            :title="lockandkey.title"
          >
            <van-panel :title="lockandkey.title" :desc="lockandkey.title">
              <van-field label="" v-model="lockandkey.meta" />
              <van-field label="">
                <template #input>
                  <van-switch v-model="lockandkey.isOn" />
                </template>
              </van-field>
              <template #footer>
                <van-button size="small" type="danger" @click="removeKey(lockandkey)"></van-button>
              </template>
            </van-panel>
          </van-collapse-item>
          <van-cell title="" name="new">
            id{{keysList.length}}
            <van-button size="small" type="warning" @click="newKey"></van-button>
          </van-cell>
        </van-collapse>
        <van-collapse v-model="activeEvents">
          <van-collapse-item v-for="event in eventsList" :key="event.id" :title="event.title">
            <van-panel title="id" desc="" :status="event.status">
              <van-field label="" v-model="event.meta" />
              <div>
                <van-field label="" v-model="event.id" readonly />
                <van-field label="" v-model="event.bind" />
                <van-field label="">
                  <template #input>
                    <van-switch v-model="event.once" />
                  </template>
                </van-field>
                <van-field label="">
                  <template #input>
                    <van-radio-group v-model="event.type" direction="horizontal">
                      <van-radio name="orign"></van-radio>
                      <van-radio name="touch"></van-radio>
                      <van-radio name="use_item"></van-radio>
                    </van-radio-group>
                  </template>
                </van-field>
                <van-col span="12">
                  <van-field label="" v-model="event.animate.id"></van-field>
                </van-col>
                <van-col span="12">
                  <van-field label="" v-model="event.animate.onFrame">1</van-field>
                </van-col>

                <van-field label="id()" v-model="event.item"></van-field>
                <van-item v-for="eventLock in event.lock" :key="eventLock.id">
                  <van-col span="12">
                    <van-field label="" v-model="eventLock.id"></van-field>
                  </van-col>
                  <van-col span="12">
                    <van-field label="">
                      <template #input>
                        <van-switch v-model="eventLock.isOn" />
                      </template>
                    </van-field>
                  </van-col>
                </van-item>
                <van-button size="small" type="danger" @click="addEventLock(event)"></van-button>
                <van-field label="" v-model="event.on"></van-field>
              </div>
              <template #footer>
                <van-button size="small" type="danger" @click="removeEvent(event)"></van-button>
              </template>
            </van-panel>
          </van-collapse-item>
          <van-cell title="" name="new">
            id{{eventsList.length}}
            <van-button size="small" type="warning" @click="newEvent"></van-button>
          </van-cell>
        </van-collapse>
        <van-collapse v-model="activeItems">
          <van-collapse-item v-for="item in itemsLists" :key="item.id" :title="item.title">
            <van-panel :title="item.title" :desc="item.title">
              <van-field label="" v-model="item.meta" />
              <div>
                <van-field label="" v-model="item.id" readonly />
                <van-field label="()" v-model="item.name" />
                <van-field label="" v-model="item.res" />
              </div>
              <template #footer>
                <van-button size="small" type="danger" @click="removeItem(item)"></van-button>
              </template>
            </van-panel>
          </van-collapse-item>
          <van-cell title="" name="new">
            id{{itemsLists.length}}
            <van-button size="small" type="warning" @click="newItem"></van-button>
          </van-cell>
        </van-collapse>
        <van-collapse v-model="activeActions">
          <van-collapse-item v-for="action in actionsList" :key="action.id" :title="action.title">
            <van-panel title="id" desc="">
              <van-field label="" v-model="action.id" readonly />
              <van-field label="" v-model="action.meta" />
              <van-field label="" v-model="action.actor" />
              <van-field name="radio" label="">
                <template #input>
                  <van-radio-group v-model="action.todo.type" direction="horizontal">
                    <van-radio name="simple"></van-radio>
                    <van-radio name="tween"></van-radio>
                    <van-radio name="item"></van-radio>
                    <van-radio name="wait"></van-radio>
                    <van-radio name="key"></van-radio>
                    <!-- <van-radio name="lock"></van-radio> -->
                    <van-radio name="clear"></van-radio>
                    <van-radio name="over"></van-radio>
                    <van-radio name="sound"></van-radio>
                    <van-radio name="a_play"></van-radio>
                    <van-radio name="a_stop"></van-radio>
                  </van-radio-group>
                </template>
              </van-field>
              <van-field label="X" v-model="action.todo.props.x" />
              <van-field label="Y" v-model="action.todo.props.y" />
              <van-field label="ScaleX" v-model="action.todo.props.scaleX" />
              <van-field label="ScaleY" v-model="action.todo.props.scaleY" />
              <van-field label="alpha" v-model="action.todo.props.alpha" />
              <van-field label="" v-model="action.todo.props.rotation" />
              <van-field label="Visible">
                <template #input>
                  <van-switch v-model="action.todo.props.visible" />
                </template>
              </van-field>
              <van-field label="" v-model="action.todo.item" />
              <van-col span="8">
                <van-field label="" v-model="action.todo.animate.id" />
              </van-col>
              <van-col span="8">
                <van-field label="" v-model="action.todo.animate.frame" />
              </van-col>
              <van-col span="8">
                <van-field label="">
                  <template #input>
                    <van-switch v-model="action.todo.animate.loop" />
                  </template>
                </van-field>
              </van-col>
              <van-col span="12">
                <van-field label="" v-model="action.todo.key.id" />
              </van-col>
              <van-col span="12">
                <van-field label="">
                  <template #input>
                    <van-switch v-model="action.todo.key.isOn" />
                  </template>
                </van-field>
              </van-col>
              <van-field label="" v-model="action.todo.time" />
              <van-field label="" v-model="action.todo.res" />

              <template #footer>
                <van-button size="small" type="danger" @click="removeAction(action)"></van-button>
              </template>
            </van-panel>
          </van-collapse-item>
          <van-cell title="" name="new">
            id{{actionsList.length}}
            <van-button size="small" type="warning" @click="newTenAction"></van-button>
          </van-cell>
          <van-cell title="" name="new">
            id{{actionsList.length}}
            <van-button size="small" type="warning" @click="newAction"></van-button>
          </van-cell>
        </van-collapse>
        <van-field label="" v-model="hints" />
        <van-collapse v-model="activeAnimates">
          <van-collapse-item
            v-for="animate in animatesList"
            :key="animate.id"
            :title="animate.title"
          >
            <van-panel :title="animate.title" :desc="animate.title">
              <van-field label="" v-model="animate.meta" />
              <van-field label="" v-model="animate.name" />
              <van-field label="" v-model="animate.res" />
              <template #footer>
                <van-button size="small" type="danger" @click="removeKey(lockandkey)"></van-button>
              </template>
            </van-panel>
          </van-collapse-item>
          <van-cell title="" name="new">
            id{{animatesList.length}}
            <van-button size="small" type="warning" @click="newAnimate"></van-button>
          </van-cell>
        </van-collapse>
        <van-button size="large" type="danger" @click="testProject"></van-button>
        <van-button size="large" type="danger" @click="saveProject"></van-button>
        <van-button size="large" type="danger" @click="loadProject"></van-button>
        <van-button size="large" type="danger" @click="generateString"></van-button>
        <van-divider></van-divider>
        <div>{{stage_script}}</div>
        <van-divider></van-divider>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import {
  Collapse,
  CollapseItem,
  Panel,
  Button,
  Form,
  Toast,
  Field,
  RadioGroup,
  Radio,
  Cell,
  Switch,
  Divider,
  Row,
  Col,
  Popup
  // GoodsActionIcon,
  // GoodsActionButton
} from "vant";

import datas from "../../data";

export default {
  components: {
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [Panel.name]: Panel,
    [Button.name]: Button,
    [Form.name]: Form,
    [Toast.name]: Toast,
    [Field.name]: Field,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Cell.name]: Cell,
    [Switch.name]: Switch,
    [Divider.name]: Divider,
    [Row.name]: Row,
    [Col.name]: Col,
    [Popup.name]: Popup
  },

  data() {
    return {
      selectURL: "resource/",
      inputStageId: 1,
      keysList: [],
      eventsList: [],
      itemsLists: [],
      actionsList: [],
      animatesList: [],
      activeKeys: ["1"],
      activeEvents: ["1"],
      activeItems: ["1"],
      activeActions: ["1"],
      activeAnimates: ["1"],
      stage_script: "",
      hints: "",
      showLog:window.showLog,
      logMsg:window.logMsg
    };
  },

  methods: {
    //----------------------------Key&Lock-----------------------------
    newKey() {
      var key = {};
      let id = this.keysList.length;
      key.id = id;
      key.meta = "";
      key.title = "" + id;
      key.isOn = true;
      key.status = "";
      key.isNew = true;
      this.keysList.push(key);
    },

    removeKey(key) {
      this.keysList.splice(key.id, 1);
      for (var i = 0; i < this.keysList.length; i++) {
        this.keysList[i].id = i;
        this.keysList[i].title = "" + i;
      }
    },

    //------------------------------Event------------------------------
    newEvent() {
      Toast(JSON.stringify(datas.events));
      var event = {};
      let id = this.eventsList.length;
      event.id = id;
      (event.meta = ""), (event.title = "" + id);
      event.bind = "mActor";
      event.type = "touch";
      event.item = null;
      event.on = "";
      event.lock = [];
      event.animate = {
        id: null,
        onFrame: 1
      };
      event.once = false;
      event.status = "";
      event.isNew = true;
      // console.log(key);
      this.eventsList.push(event);
    },

    addEventLock(event) {
      var lock0 = {};
      lock0.id = 0;
      lock0.isOn = false;
      event.lock.push(lock0);
    },

    removeEvent(event) {
      Toast(JSON.stringify(event));
      this.eventsList.splice(event.id, 1);
      for (var i = 0; i < this.eventsList.length; i++) {
        this.eventsList[i].id = i;
        this.eventsList[i].title = "" + i;
      }
    },

    //------------------------------Item------------------------------

    newItem() {
      var item = {};
      let id = this.itemsLists.length;
      item.id = id;
      (item.meta = ""), (item.title = "" + id);
      item.name = "";
      item.res = "_png";
      item.status = "";
      item.isNew = true;
      // console.log(key);
      this.itemsLists.push(item);
    },

    removeItem(item) {
      Toast(JSON.stringify(item));
      this.itemsLists.splice(item.id, 1);
      for (var i = 0; i < this.itemsLists.length; i++) {
        this.itemsLists[i].id = i;
        this.itemsLists[i].title = "" + i;
      }
    },

    //------------------------------Action------------------------------
    newTenAction() {
      for (let i = 0; i < 10; i++) {
        this.newAction();
      }
    },

    newAction() {
      var action = {};
      let id = this.actionsList.length;
      action.id = id;
      action.meta = "" + id + "";
      action.title = "" + id;
      action.actor = "mActor";
      action.todo = {
        props: {
          x: null,
          y: null,
          visible: true,
          scaleX: null,
          scaleY: null,
          alpha: null,
          rotation: null
        },
        type: "simple",
        time: 0,
        item: null,
        key: {},
        res: "",
        animate: {}
      };
      action.todo.key.id = null;
      action.todo.key.isOn = false;
      action.status = "";
      action.isNew = true;
      action.todo.animate.id = null;
      action.todo.animate.frame = 0;
      action.todo.animate.loop = false;
      // console.log(key);
      this.actionsList.push(action);
    },

    removeAction(action) {
      Toast(JSON.stringify(action));
      this.actionsList.splice(action.id, 1);
      for (var i = 0; i < this.actionsList.length; i++) {
        this.actionsList[i].id = i;
        this.actionsList[i].title = "" + i;
      }
    },

    //------------------------------Animate------------------------------
    newAnimate() {
      var animate = {};
      let id = this.animatesList.length;
      animate.id = id;
      animate.title = "" + id;
      animate.status = "";
      animate.isNew = true;
      animate.name = "";
      animate.res = "_json";
      // // console.log(key);
      this.animatesList.push(animate);
    },

    //------------------------------------------------------------------

    testProject() {
      window.inputStageId = this.inputStageId;
      window.selectURL = this.selectURL;

      window.stageScript = this.generateJSON();
      document.getElementById("iframe").contentWindow.location.reload(true);
    },

    saveProject() {
      var content = JSON.stringify({
        keysList: this.keysList,
        eventsList: this.eventsList,
        itemsLists: this.itemsLists,
        actionsList: this.actionsList,
        animatesList: this.animatesList
      });
      var filename = "project.json";
      var blob;
      var eleLink = document.createElement("a");
      if ("download" in eleLink) {
        eleLink.download = filename;
        eleLink.style.display = "none";
        blob = new Blob([content]);
        eleLink.href = window.URL.createObjectURL(blob);
        document.body.appendChild(eleLink);
        eleLink.click();
        document.body.removeChild(eleLink);
      } else {
        //
        blob = new Blob([content]);
        window.navigator.msSaveOrOpenBlob(blob, filename);
      }
      // var projectJSON = {
      //   keysList: this.keysList,
      //   eventsList: this.eventsList,
      //   itemsLists: this.itemsLists,
      //   actionsList: this.actionsList,
      //   animatesList: this.animatesList
      // };
    },

    loadProject() {
      var url = "./stage_script.json";
      var data = null;
      var xhr = new XMLHttpRequest();
      xhr.open("GET", url);
      xhr.overrideMimeType("application/json");
      xhr.onload = () => {
        if (xhr.status < 400) {
          Toast(xhr.responseText);

          data = JSON.parse(xhr.responseText);
          // 
          if (data != null) {
            this.keysList = data["keysList"];
            this.eventsList = data["eventsList"];
            this.itemsLists = data["itemsLists"];
            this.actionsList = data["actionsList"];
            this.animatesList = data["animatesList"];
          }
        }
      };
      xhr.onerror = function() {};
      xhr.send();
    },

    generateString() {
      var stage = this.generateJSON();
      this.stage_script = JSON.stringify(stage);
    },

    generateJSON() {
      var stage = {
        keys: [],
        events: [],
        items: [],
        actions: [],
        animates: [],
        hints: this.hints
      };
      // if (this.keysList !== "") {
      //   let ary = this.keysList.split(",");
      //   ary.forEach(keyElement => {
      //     stage.keys.push(Number(keyElement));
      //   });
      // }
      this.keysList.forEach(element => {
        var key = {};
        key.id = element.id;
        key.isOn = element.isOn;
        stage.keys.push(key);
      });

      this.eventsList.forEach(element => {
        var event = {};
        event.id = element.id;
        event.bind = element.bind;
        event.type = element.type;
        // event.on = element.on.split(",");
        event.on = [];
        if (element.on !== "") {
          let ary = element.on.split(",");
          ary.forEach(onElement => {
            event.on.push(Number(onElement));
          });
        }
        if (element.animate.id !== null) {
          event.animate = element.animate;
        }
        // event.on = element.on;
        event.once = element.once;
        if (element.lock.id !== null) {
          event.lock = element.lock;
        }
        event.item = element.item;
        stage.events.push(event);
      });
      this.itemsLists.forEach(element => {
        var item = {};
        item.id = element.id;
        item.name = element.name;
        item.res = element.res;
        stage.items.push(item);
      });
      this.actionsList.forEach(element => {
        var action = {};
        action.id = element.id;
        action.actor = element.actor;
        action.todo = {};
        action.todo.props = {};
        action.todo.type = element.todo.type;
        switch (action.todo.type) {
          case "simple":
            if (element.todo.props.x !== null) {
              action.todo.props.x = Number(element.todo.props.x);
            }
            if (element.todo.props.y !== null) {
              action.todo.props.y = Number(element.todo.props.y);
            }
            if (element.todo.props.scaleX !== null) {
              action.todo.props.scaleX = Number(element.todo.props.scaleX);
            }
            if (element.todo.props.scaleY !== null) {
              action.todo.props.scaleY = Number(element.todo.props.scaleY);
            }
            if (element.todo.props.alpha !== null) {
              action.todo.props.alpha = Number(element.todo.props.alpha);
            }
            if (element.todo.props.visible !== null) {
              action.todo.props.visible = Boolean(element.todo.props.visible);
            }
            if (element.todo.props.rotation !== null) {
              action.todo.props.rotation = Number(element.todo.props.rotation);
            }
            break;
          case "tween":
            if (element.todo.props.x !== null) {
              action.todo.props.x = Number(element.todo.props.x);
            }
            if (element.todo.props.y !== null) {
              action.todo.props.y = Number(element.todo.props.y);
            }
            if (element.todo.props.visible !== null) {
              action.todo.props.visible = Boolean(element.todo.props.visible);
            }
            if (element.todo.props.rotation !== null) {
              action.todo.props.rotation = Number(element.todo.props.rotation);
            }
            action.todo.time = Number(element.todo.time);
            break;
          case "item":
            action.todo.item = element.todo.item;
            break;
          case "wait":
            action.todo.time = element.todo.time;
            break;
          case "key":
            if (element.todo.key.id !== null) {
              action.todo.key = element.todo.key;
            }
            break;
          case "a_play":
            if (element.todo.animate.id !== null) {
              if (element.todo.frame === 0) {
                action.todo.frame = 1;
              }
              action.todo.animate = {};
              if (element.todo.frame === 0) {
                action.todo.frame = 1;
              } else {
                action.todo.frame = element.todo.frame;
              }
              action.todo.id = element.todo.id;
              action.todo.isLoop = element.todo.isLoop;
            }
            break;
          case "a_stop":
            if (element.todo.animate.id !== null) {
              if (element.todo.frame === 0) {
                action.todo.frame = 1;
              }
              action.todo.animate = {};
              if (element.todo.frame === 0) {
                action.todo.frame = 1;
              } else {
                action.todo.frame = element.todo.frame;
              }
              action.todo.id = element.todo.id;
              action.todo.isLoop = element.todo.isLoop;
            }
            break;
          // case "lock":
          //   action.todo.key = Number(element.todo.key);
          // break;
          case "clear":
            action.todo.res = element.todo.res;
            break;
          case "over":
            action.todo.res = element.todo.res;
            break;
          case "sound":
            action.todo.res = element.todo.res;
            break;
        }
        stage.actions.push(action);
      });
      this.animatesList.forEach(element => {
        var animate = {};
        animate.id = element.id;
        animate.name = element.name;
        animate.res = element.res;
        stage.animates.push(animate);
      });
      return stage;
    }
  }
};
</script>

<style lang="less">
.goods {
  padding-bottom: 50px;

  &-swipe {
    img {
      width: 100%;
      display: block;
    }
  }

  &-title {
    font-size: 16px;
  }

  &-price {
    color: #f44;
  }

  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }

  &-cell-group {
    margin: 15px 0;

    .van-cell__value {
      color: #999;
    }
  }

  &-tag {
    margin-left: 5px;
  }
}
</style>
